<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">Your Crew</h1>
          <h2 class="subtitle is-4">
            Buy ticekts for or invite others
          </h2>
        </div>
      </div>
    </section>

    <section class="container section-spacing content">
      <div class="columns is-centered">
        <div class="column is-8">
          <p class="is-size-4">
            <span class="has-text-primary">{{ personName }}</span>
          </p>

          <CopyInviteAndBuy
            :allocationLeft="allocationLeft"
            :event="event"
          />

          <p>
            <strong class="has-text-primary">
              The TEDxAuckland team
            </strong>
          </p>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-9">
          <div class="columns">
            <div class="column is-8">
              <div class="ticket-container">
                <h2 class="title is-3">Your Ticket Details</h2>

                <TicketPreview
                  :person="person"
                  :ticket-offer="ticketOffer"
                  :event="event"
                />
              </div>

              <div class="ticket-container columns">
                <div class="column">
                  <h2 class="title is-3">Purchase Additional Tickets</h2>

                  <TicketPurchaseFor
                    v-for="(v, index) in $v.tickets.$each.$iter"
                    :key="index"
                    :$v="v"
                    v-model="v.$model"
                    @remove="removeTicket(index)"
                  />

                  <button
                    class="add-button button is-primary is-medium is-pulled-right"
                    type="button"
                    :disabled="allocationReached"
                    @click.prevent="addTicket"
                  >
                    Add Ticket
                  </button>
                </div>
              </div>

              <div class="ticket-container columns">
                <div class="column">
                  <h2 class="title is-3">Invite Other People</h2>

                  <TicketPurchaseFor
                    v-for="(v, index) in $v.invites.$each.$iter"
                    v-model="v.$model"
                    :$v="v"
                    @remove="removeInvite(index)"
                  />

                  <button
                    class="add-button button is-primary is-medium is-pulled-right"
                    type="button"
                    :disabled="allocationReached"
                    @click.prevent="addInvite"
                  >
                    Add Invite
                  </button>
                </div>
              </div>
            </div>
            <div class="column is-4">
              <OrderTally
                :person="person"
                :tickets="$v.tickets.$each.$iter"
                :invites="$v.invites.$each.$iter"
                :ticketPrice="ticketOffer.costAfterDiscountCents * 1"
                @checkout="checkout"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { required, email } from "vuelidate/lib/validators";
import Markdown from "@/components/Markdown";
import CopyInviteAndBuy from "@/ticketing/components/CopyInviteAndBuy";
import TicketPreview from "@/ticketing/components/TicketPreview";
import TicketPurchaseFor from "@/ticketing/components/TicketPurchaseFor";
import OrderTally from "@/ticketing/components/OrderTally";
import TicketService from "@/services/TicketService";
import NProgress from "nprogress";
import { personNameMixin } from "@/mixins/personNameMixin";

export default {
  name: "TicketOfferPurchase",
  components: {
    Markdown,
    CopyInviteAndBuy,
    TicketPreview,
    TicketPurchaseFor,
    OrderTally
  },
  props: {
    person: {
      type: Object,
      required: true
    },
    ticketOffer: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    },
    newRecipient: {
      type: Object,
      required: true
    }
  },
  mixins: [personNameMixin],
  data() {
    return {
      isRejectModalActive: false,
      tickets: [],
      invites: []
    };
  },
  computed: {
    formattedTicketPrice() {
      return `$${this.ticketOffer.costAfterDiscountCents / 100}`;
    },
    ticketsLeft() {
      return this.event.standardTicketsLeft;
    },
    allocationLeft() {
      return (
        this.ticketOffer.ticketCount -
        1 -
        this.tickets.length -
        this.invites.length
      );
    },
    allocationReached() {
      return this.allocationLeft <= 0;
    }
  },
  validations: {
    tickets: {
      required,
      $each: {
        firstName: { required },
        lastName: { required },
        email: { required, email }
      }
    },
    invites: {
      required,
      $each: {
        firstName: { required },
        lastName: { required },
        email: { required, email }
      }
    }
  },
  methods: {
    addTicket() {
      this.allocationReached
        ? null
        : this.tickets.push(Object.assign({}, this.newRecipient));
    },
    removeTicket(index) {
      this.tickets.splice(index, 1);
    },
    addInvite() {
      this.allocationReached
        ? null
        : this.invites.push(Object.assign({}, this.newRecipient));
    },
    removeInvite(index) {
      this.invites.splice(index, 1);
    },
    checkout() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();
        TicketService.acceptTicket({
          id: this.ticketOffer.id,
          contactQuestions: this.person,
          ticketQuestions: {
            ...this.applicationQuestions,
            ...this.ticketQuestions
          }
        })
          .then(response => {
            NProgress.done();
            window.location.assign(response.data.purchase_url);
          })
          .catch(e => {
            NProgress.done();
            console.log("ERROR", e);
          });
      }
    }
  }
};
</script>

<style scoped>
.add-button {
  margin-right: 1rem;
}
</style>
