<template>
  <aside>
    <h3 class="title is-4">Order Tally</h3>

    <h4 class="title is-5">Tickets</h4>
    <div class="tally-row">
      <div class="tally-row__name">
        {{ person.firstName }}
      </div>
      <div class="tally-row__price">
        {{ formattedTicketPrice }}
      </div>
    </div>

    <div v-for="(ticket, index) in tickets" class="tally-row">
      <template v-if="ticket.$invalid">
        <div class="tally-row__name">
          complete form or remove
        </div>
        <div class="tally-row__price">
          ---
        </div>
      </template>
      <template v-else>
        <div class="tally-row__name">
          {{ ticket.$model.firstName }}
        </div>
        <div class="tally-row__price">
          {{ formattedTicketPrice }}
        </div>
      </template>
    </div>

    <h4 class="title is-5">Invites</h4>
    <div v-for="(invite, index) in invites" class="tally-row">
      <template v-if="!invite.$invalid">
        <div class="tally-row__name">
          {{ invite.$model.firstName }}
        </div>
        <div class="tally-row__price">
          free
        </div>
      </template>
    </div>

    <div class="tally-row">
      <div class="tally-row__name">
        total:
      </div>
      <div class="tally-row__price">
        {{ formattedOrderTotal }}
      </div>
    </div>
    <button
      class="button is-primary is-medium is-fullwidth"
      type="button"
      @click.prevent="$emit('checkout')"
    >
      Checkout
    </button>
  </aside>
</template>

<script>
export default {
  name: "OrderTally",
  props: {
    person: {
      type: Object,
      required: true
    },
    tickets: {
      type: Object,
      required: true
    },
    invites: {
      type: Object,
      required: true
    },
    ticketPrice: {
      type: Number,
      required: true
    }
  },
  computed: {
    formattedTicketPrice() {
      return `$${this.ticketPrice / 100}`;
    },
    validTicketCount() {
      return Object.values(this.tickets).filter(ticket => !ticket.$invalid).length;
    },
    orderTotal() {
      return (1 + this.validTicketCount) * this.ticketPrice;
    },
    formattedOrderTotal() {
      return `$${this.orderTotal / 100}`;
    },
    plural() {
      return this.ticketOffer.ticketCount > 1;
    },
    ticketsLeft() {
      return this.event.standardTicketsLeft;
    }
  }
};
</script>

